<template>
  <v-container class="fill-height">
    <v-sheet
      elevation="2"
      class="pa-2 mx-auto d-flex fill-height flex-grow-1 flex-column rounded-sm"
      max-width="600"
    >
      <RouteItem :disabled="true" :routeItem="kmbRouteData" />
      <div class="flex-grow-1 py-2" ref="scrollBoxContainer">
        <v-timeline align-top dense>
          <v-virtual-scroll
            :items="routeStopsCache"
            :item-height="100"
            :height="scrollBoxHeight"
            class="overflow-x-hidden"
          >
            <template v-slot:default="{ item }">
              <v-timeline-item color="pink">
                <div class="d-flex align-center">
                  <strong class="flex-grow-1"
                    >{{ item.seq }}:{{ item.name_tc }}</strong
                  >
                  <v-btn
                    v-if="isInBookmark(item)"
                    class="ma-2"
                    text
                    icon
                    color="red lighten-2"
                    @click="removeBookmark(item)"
                  >
                    <v-icon x-large>mdi-bookmark-check</v-icon>
                  </v-btn>
                  <v-btn
                    v-else
                    class="ma-2"
                    text
                    icon
                    color="lighten-2"
                    @click="addBookmark(item)"
                  >
                    <v-icon x-large>mdi-bookmark-outline</v-icon>
                  </v-btn>
                </div>
              </v-timeline-item>
            </template>
          </v-virtual-scroll>
        </v-timeline>
      </div>
    </v-sheet>
    <!-- TODO : loading ,  -->
  </v-container>
</template>

<script>
import RouteItem from "@/components/RouteItem.vue";
import { mapGetters } from "vuex";
import { actionTypes } from "@/store/store-types";
export default {
  components: {
    RouteItem,
  },
  props: {
    route: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      scrollBoxHeight: 0,
      routeStopsCache: [],
    };
  },
  computed: {
    ...mapGetters(["kmbRouteData", "getStopsInfo", "bookmarkStops"]),
  },
  async mounted() {
    // cal scroll Box Height
    this.scrollBoxHeight = this.$refs.scrollBoxContainer.clientHeight - 42;

    // get route info by query
    const { route, bound, serviceType } = this.$route.query;

    // get route data
    await this.$store.dispatch(actionTypes.fetchKmbRoute, {
      route,
      bound,
      serviceType,
    });
    // get stops cache
    if (route && bound && serviceType) {
      await this.$store.dispatch(actionTypes.genKmbRouteStopsInfoCache, {
        route,
        bound,
        serviceType,
      });
      this.routeStopsCache = await this.getStopsInfo(route, bound, serviceType);
    }
  },
  methods: {
    isInBookmark(item) {
      const index = this.bookmarkStops.findIndex(
        (x) =>
          x.stopInfo.bound === item.bound &&
          x.stopInfo.stop === item.stop &&
          x.stopInfo.service_type === item.service_type &&
          x.stopInfo.route === item.route
      );
      return index > -1;
    },
    addBookmark(stopItem) {
      this.$store.dispatch(actionTypes.addBookmarkStop, {
        route: stopItem.route,
        bound: stopItem.bound,
        serviceType: stopItem.service_type,
        stop: stopItem.stop,
      });
    },
    removeBookmark(stopItem) {
      this.$store.dispatch(actionTypes.removeBookmarkStop, {
        route: stopItem.route,
        bound: stopItem.bound,
        serviceType: stopItem.service_type,
        stop: stopItem.stop,
      });
    },
  },
};
</script>

