<template>
  <v-hover v-slot="{ hover }">
    <v-card
      :elevation="!disabled && hover ? 12 : 2"
      class="d-flex pa-2 mx-sm-auto"
      width="100%"
      max-width="500"
    >
      <v-avatar color="red darken-3" size="64" rounded>
        <span class="white--text text-h5">{{ routeItem.route }}</span>
      </v-avatar>
      <v-sheet class="flex-grow-1 d-flex flex-column align-stretch px-2">
        <span class="caption font-weight-light"
          >{{ $t("transport.from") }} {{ routeItem.orig_tc }}</span
        >
        <v-sheet class="flex-grow-1 d-flex align-center text-left">
          <div class="d-flex flex-column">
            <span class="caption font-weight-light">{{
              $t("transport.to")
            }}</span>
            <v-icon>mdi-ray-start-arrow</v-icon>
          </div>
          <span class="text-h5 px-2">{{ routeItem.dest_tc }}</span>
        </v-sheet>
      </v-sheet>
      <v-sheet v-if="!disabled">
        <v-btn
          @click="clickItem(routeItem)"
          color="grey"
          outlined
          depressed
          x-small
          height="100%"
        >
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </v-sheet>
    </v-card>
  </v-hover>
</template>

<script>
export default {
  name: "RouteItem",
  props: {
    routeItem: {
      type: Object,
      default: () => {},
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    clickItem(item) {
      this.$emit("onClick", item);
    },
  },
};
</script>

